import { getElementError } from "@testing-library/dom";
import React, { useState } from "react";

export default function TextForm(props) {
    const handleUpClick = () => {
        // console.log("Uppercase was clicked", + text);
        let newText = text.toUpperCase();
        setText(newText)
        props.showAlert("Converted to uppercase!", "success");
    }
    const handleLoClick = () => {
        // console.log("Uppercase was clicked", + text);
        let newText = text.toLowerCase();
        setText(newText)
        props.showAlert("Converted to lowercase!", "success");
    }

    const handleCopy = () => {

        console.log("i am copy");
        var text = document.getElementById("myBox");
        text.select();
        navigator.clipboard.writeText(text.value);
        props.showAlert("Copy text!", "success");
    }

    const handleExtraSpace = () => {
        // console.log('remove extra space')
        let newText = text.split(/[ ]+/);
        setText(newText.join(" "))
        props.showAlert("Remove extra space!", "success");
    }



    const handleClearClick = () => {
        // console.log("Uppercase was clicked", + text);
        let newText = '';
        setText(newText)
        props.showAlert("Clear text!", "success");
    }
    const handleOnChange = (event) => {
        console.log("On change");
        setText(event.target.value);
    }
    const [text, setText] = useState('Enter text here ?');
    //text = "new text"; // Wrong way to change state //
    //setText = ("new text"); // Correct way to change state //
    return (
        <>
            <div className="container" style={{ color: props.mode === 'dark' ? 'white' : 'black' }}>
                <h1>{props.heading}</h1>
                <div className="mb-3">
                    <textarea className="form-control" value={text} onChange={handleOnChange} style={{ backgroundColor: props.mode === 'dark' ? '#c5cae9' : 'white', color: props.mode === 'dark' ? 'white' : 'black' }} id="myBox" rows="7"></textarea>
                </div>
                <button className="btn btn-primary mx-22" onClick={handleUpClick}>convert to Uppercase</button>

                <button className="btn btn-primary mx-2" onClick={handleLoClick}>convert to Lowercase</button>

                <button className="btn btn-primary mx-2" onClick={handleCopy}>Copy Text</button>

                <button className="btn btn-primary mx-2" onClick={handleClearClick}>  ClearText </button>

                <button className="btn btn-primary mx-2" onClick={handleExtraSpace}> Remove Extra Space </button>

                <div className=" container my-2">
                    <h1>your text summary </h1>
                    <p>{text.split(" ").length} words, {text.length} character</p>
                    <p>{0.008 * text.split(" ").length} minute read </p>
                    <h2>preview</h2>
                    <p>{text}</p>
                </div>
            </div >
        </>
    )

}